/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-create-block-vayu-blocks {
	border: 1px dotted #f00;
}

.editor-styles-wrapper .wp-block-vayu-blocks-advance-heading{
	--h-background:none;
	--h-background-hover: var(--h-background);
	--transition:0.2s;
	--h-boxShadow-hvr:var(--h-boxShadow);
	--background-attachment-hvr:var(--h-background-attachment);
	--background-position-hvr:var(--h-background-position);
	--background-repeat-hvr:var(--h-background-repeat);
	--background-size-hvr:var(--h-background-size);
	--h-border-top-width-hvr:var(--h-border-top-width);
	--h-border-bottom-width-hvr:var(--h-border-bottom-width);
	--h-border-right-width-hvr:var(--h-border-right-width);
	--h-border-left-width-hvr:var(--h-border-left-width);
	--h-border-top-right-radius-hvr:var(--h-border-top-right-radius);
	--h-border-top-left-radius-hvr:var(--h-border-top-left-radius);
	--h-border-bottom-right-radius-hvr:var(--h-border-bottom-right-radius);
	--h-border-bottom-left-radius-hvr:var(--h-border-bottom-left-radius);
	--heading-border-hvr-style:var(--heading-border-style);
	--heading-border-hvr-color:var(--heading-border-color);
	--heading-hvr-color:var(--heading-color);
	font-family:var(--heading-font-family);
	font-weight:var(--heading-font-weight);
	font-style:var(--heading-font-style);
	text-transform:var(--heading-text-transform);
	text-align:var(--h-text-align);
	color:var(--heading-color);
	background: var(--h-background);
	background-repeat: var(--h-background-repeat);
	background-size: var(--h-background-size);
	background-position: var(--h-background-position);
	background-attachment: var(--h-background-attachment);
	padding-top:var(--h-padding-top);
	padding-right:var(--h-padding-right);
	padding-bottom:var(--h-padding-bottom);
	padding-left:var(--h-padding-left);
	margin-top:var(--h-margin-top);
	margin-bottom:var(--h-margin-bottom);
	border-style:var(--heading-border-style);
	border-color:var(--heading-border-color);
	border-top-width:var(--h-border-top-width);
	border-bottom-width:var(--h-border-bottom-width);
	border-right-width:var(--h-border-right-width);
	border-left-width:var(--h-border-left-width);
	border-top-right-radius:var(--h-border-top-right-radius);
	border-top-left-radius:var(--h-border-top-left-radius);
	border-bottom-right-radius:var(--h-border-bottom-right-radius);
	border-bottom-left-radius:var(--h-border-bottom-left-radius);
	box-shadow:var(--h-boxShadow);
	z-index:var(--h-z-index);
	font-size:var(--h-font-size);
    line-height:var(--h-line-height);
	letter-spacing:var(--h-letter-spacing);
    width:var(--h-width);
	max-width:var(--h-max-width);
	&:hover {
		color:var(--heading-hvr-color);
		background: var(--h-background-hover);
		background-repeat: var(--h-background-repeat-hvr);
		background-size: var(--h-background-size-hvr);
		background-position: var(--h-background-position-hvr);
		background-attachment: var(--h-background-attachment-hvr);
		border-style:var(--heading-border-hvr-style);
	    border-color:var(--heading-border-hvr-color);
		box-shadow:var(--h-boxShadow-hvr);
		border-top-width:var(--h-border-top-width-hvr);
		border-bottom-width:var(--h-border-bottom-width-hvr);
		border-right-width:var(--h-border-right-width-hvr);
		border-left-width:var(--h-border-left-width-hvr);
		border-top-right-radius:var(--h-border-top-right-radius-hvr);
		border-top-left-radius:var(--h-border-top-left-radius-hvr);
		border-bottom-right-radius:var(--h-border-bottom-right-radius-hvr);
		border-bottom-left-radius:var(--h-border-bottom-left-radius-hvr);
        transition:var(--h-transition);
	}
} 

.editor-styles-wrapper .block-editor-block-list__layout.is-root-container > .wp-block-vayu-blocks-advance-heading.alignwide{
	max-width:var(--h-max-width);
}

.editor-styles-wrapper .wp-block-vayu-blocks-advance-heading a{
	font-family:var(--heading-font-family);
	font-weight:var(--heading-font-weight);
	font-style:var(--heading-font-style);
	text-transform:var(--heading-text-transform);
	color:var(--heading-color);
	text-decoration:none;
	&:hover {
		color:var(--heading-hvr-color);
	}
}

@media only screen and (min-width: 1024px)  {
	.wp-block-vayu-blocks-advance-heading.th-hide-desktop:before {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: hsla(0,0%,100%,.6);
		z-index:99999999;
		
	}
	.block-editor-block-list__layout .block-editor-block-list__block.th-hide-desktop.wp-block-vayu-blocks-advance-heading{
	 position: initial;
	pointer-events:none;
	}
	
}


@media only screen and (min-width: 768px) and (max-width: 1023px) {
	.wp-block-vayu-blocks-advance-heading.th-hide-tablet:before {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: hsla(0,0%,100%,.6);
		z-index:99999999;
		
	}

.block-editor-block-list__layout .block-editor-block-list__block.th-hide-tablet.wp-block-vayu-blocks-advance-heading{
	position: initial;
	pointer-events:none;
}

}

@media only screen and (max-width: 767px) {
  
	.wp-block-vayu-blocks-advance-heading.th-hide-mobile:before {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: hsla(0,0%,100%,.6);
		z-index:99999999;
		
	}

	.block-editor-block-list__layout .block-editor-block-list__block.th-hide-mobile.wp-block-vayu-blocks-advance-heading{
	 position: initial;
	pointer-events:none;
	}

}